html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 150vh;
}

.visually-hidden {
  position: absolute;
  left: -625rem;
  top: auto;
  width: 0.0625rem;
  height: 0.0625rem;
  overflow: hidden;
}

:root {
  --white: hsl(0, 0%, 100%);
  --ltgray: hsl(212, 45%, 89%);
  --grblue: hsl(220, 15%, 55%);
  --dkblue: hsl(218, 44%, 22%);
}

:root {
  --font-outfit: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.container {
  display: grid;
  justify-items: start; /* Align to start horizontally */
  align-items: start; /* Align to start vertically */
}
@media (min-width: 90em) {
  .container {
    padding: 3;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-weight: bolder;
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

p {
  font-size: 1.3125rem;
  font-family: --font-outfit;
}

a,
a:visited,
a:hover {
  text-decoration: none;
  color: var(--white);
}

br {
  margin-top: 0.3125rem;
  margin-bottom: 0rem;
}

.body {
  box-sizing: border-box;
  font-family: "Outfit", sans-serif;
  height: 10vh;
  background: var(--ltgray);
}

.QR {
  display: flex;
  flex-direction: column;
  align-self: center;
  background-color: var(--white);
  border-radius: 1.25rem;
  padding: 1.25rem 1.25rem 4.375rem 1.25rem;
  margin: 2.313rem;
  width: 25rem;
}
@media (min-width: 90em) {
  .QR {
    margin: 20rem 35rem 2rem 35rem;
  }
}
.QR img {
  max-width: 21.875rem;
  max-height: 21.875rem;
  border-radius: 1.25rem;
}
.QR__title, .QR p {
  margin: 1.25rem 1.25rem 0 1.25rem;
}
.QR__title {
  display: grid;
  justify-content: center;
  font-size: 1.875rem;
  font-weight: 700;
  font-family: --font-outfit;
  color: var(--dkblue);
}
.QR p {
  color: var(--grblue);
  padding: 1.3125rem;
}